* {
  box-sizing: border-box;
}

:root {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 220px);
  justify-content: center;
  gap: 30px 5px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  padding: 10px;
  transition: transform 1s;
  position: relative;
}

.photo {
  aspect-ratio: 1;
  border-radius: 100px;
  width: 100%;
  margin: 0 auto 8px;
  transition: border-radius 1s;
  position: relative;
  overflow: hidden;
}

.photo img {
  width: inherit;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 32px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.details {
  text-align: center;
}

.name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 600;
  line-height: 23px;
  display: block;
}

.extra {
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  margin-top: 9px;
  padding: 0 10px 16px;
  font-size: 18px;
  line-height: 20px;
  display: none;
  position: absolute;
  left: 0;
  right: 0;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .photo {
  border-radius: 60px;
}

.person:hover .extra {
  background-color: #eee;
  display: block;
}
